<template>
  <div class="orderDetail">
    <mt-header class="header">
      <a slot="left"  @click="back">
        <mt-button icon="back">订单详情</mt-button>
      </a>
      <mt-button slot="right" class="pre" @click.native="handleClick">{{orderDetail.navtitle}}</mt-button>

    </mt-header>
    <div class="orderDetailBody">
      <div class="orderDetailBodyHeader">
        <div>
          <p style="font-size:0.4rem;color:#565256;height:0.6rem">{{orderDetail.code}}</p>
          <p style="font-size:0.3rem;color:#565256;">{{orderDetail.createdAt}}</p>
        </div>
        <div><img @click="showqrImg(orderDetail.qrcode)" src="../assets/icon_qrcode.jpg" alt=""></div>
      </div>
      <div class="orderDetailBodyMain">
        <div class="orderDetailBodyMainHeader">
          <div v-if="defaultInfo" class="hgInfo" style="flex:2">
            <img src="../assets/defaultAvatar.png" alt="">
            <span>暂未分配护工或月嫂</span>
          </div>
          <div v-if="detailInfo" class="hgInfo" style="display:flex;">
            <img @click="currentCareDetail(orderDetail.code)" :src="currentCare.avatar" alt="">
            <div style="height:auto;line-height:auto;align-self: center;margin-left:0.1rem;font-size:0.4rem;">
              <p style="height:0.3rem;">{{currentCare.name}}<span style="border:2px solid #559cf4;color:#559cf4;border-radius:10px;font-size:0.3rem;padding:0.1rem;margin-left:0.2rem;;">介护师</span></p>
              <p style="font-size:0.3rem;color:#d06091;margin-top:0.1rem;">{{currentCare.sex | sexFilter}} {{currentCare.age}}岁 {{currentCarenativePlace}}</p>
            </div>
          </div>
          <div class="hgStatus">{{orderDetail.hgStatus}}</div>
        </div>
        <div class="orderDetailBodyMainBody">
          <div><span class="t">订单金额</span>¥{{orderDetail.amount}}</div>
          <div>
            <span class="t">被护理人</span>
            <span>{{orderDetail.beCare.name}} <img v-if="orderDetail.beCare.sex" style="height:0.3rem;margin-left:0.2rem;" src="../assets/female.png" alt=""><img style="height:0.3rem;margin-left:0.2rem;" v-else src="../assets/male.png" alt=""></span>
            <span>{{orderDetail.beCare.age}}岁 {{orderDetail.beCare.nation | nationFilter}}</span>
          </div>
          <div><span class="t">联系电话</span>{{orderDetail.beCare.phone}}</div>
          <div><span class="t" style="letter-spacing: 0.8rem">亲属</span><span>{{orderDetail.beCare.family}}</span><span>18379910278</span></div>
          <div><span class="t">服务地址</span>{{orderDetail.address}}</div>
          <div><span class="t">服务类型</span>{{orderDetail.serviceType | serviceTypeFilter}}</div>
          <div><span class="t">陪护分类</span>{{orderDetail.serviceName}}</div>
          <div>
            <span class="t">开始时间</span>
            <ul v-if="orderDetail.serviceDates">
              <li v-for="time in orderDetail.serviceDates">{{time}}</li>
            </ul>
             <span v-if="orderDetail.serviceDate">{{orderDetail.serviceDate}}</span>
          </div>
          <div><span class="t">上工时间</span>{{orderDetail.workBeginTime | blankFilter}}</div>
          <div><span class="t">下工时间</span>{{orderDetail.workEndTime | blankFilter}}</div>
        </div>
      </div>
    </div>
    <div class="orderDetailbtns">
      <mt-button v-if="orderDetail.careDetail" @click.native="careInfo(orderDetail.code)"  type="default">护理信息查看</mt-button>
      <mt-button v-if="orderDetail.changeCare" @click.native="changeCare" type="default">换工</mt-button>
      <mt-button v-if="orderDetail.renew" @click.native="renew" type="default">续约</mt-button>
      <mt-button v-if="orderDetail.evaluate" @click.native="evaluate" type="default">评价</mt-button>
    </div>
     <mt-popup v-model="qrcode" popup-transition="popup-fade">
      <img style="width:100%;height:100%;" :src="orderDetail.qrcode" alt="">
    </mt-popup>
    <div class="msgbox" v-if="show">
      <div class="m_header">
        <div class="m_title">提示</div>
      </div>
      <div class="m_content">
        <div class="m_message">
          <textarea v-model="reason" class="w_field_core" style="margin-bottom:none;" cols="30" rows="5" placeholder="请输入换工申请原因"></textarea>
        </div>
      </div>
      <div class="m_btns">
        <button @click="handleCancle" class="btn btn_cancel">取消</button>
        <button @click="handleRight" class="btn btn_right">确定</button>
      </div>
    </div>
    <div class="msgbox" v-if="showrenew">
      <div class="m_header">
        <div class="m_title">提示</div>
      </div>
      <div class="m_content">
        <div class="m_message">
          <p style="font-size:0.4rem;">{{renewMessage}}</p>
        </div>
      </div>
      <div class="m_btns">
        <button @click="showrenew = false" class="btn btn_cancel">取消</button>
        <button @click="showDayHandle" class="btn btn_right">确定</button>
      </div>
    </div>
    <div class="msgbox" v-if="showFinishOrder">
      <div class="m_header">
        <div class="m_title">提示</div>
      </div>
      <div class="m_content">
        <div class="m_message">
           <p style="font-size:0.4rem;">{{orderText}}</p>
        </div>
      </div>
      <div class="m_btns">
        <button @click="showFinishOrder = false" class="btn btn_cancel">取消</button>
        <button @click="handleshowFinishOrderRight" class="btn btn_right">确定</button>
      </div>
    </div>
    <div class="msgbox cares" v-if="showCares">
      <div class="m_header">
        <div class="m_title">选择要评价的护工</div>
      </div>
      <div class="m_content">
        <div class="m_message">
           <div class="info" v-for="care in cares" @click="handleCareClick(code,care.id,orderDetail.serviceType)">
             <img :src="care.avatar" alt="">
             <p>{{care.name}}</p>
           </div>
        </div>
      </div>
      <div class="m_btns">
        <button @click="showCares = false" class="btn btn_cancel">取消</button>
      </div>
    <mt-popup v-model="showDay" popup-transition="popup-fade">
        <mt-button @click.native="handleInnerSecondChange" size="small">确定</mt-button>
        <mt-picker :slots="daySlots" @change="onDayChange" :visible-item-count="5"></mt-picker>
     </mt-popup>
  </div>
</template>


<script>
import { mapGetters, mapActions } from 'vuex'
import { Toast, MessageBox } from 'mint-ui';
import {dayArr} from '../utils/oneMonthTime'
import {timeFilter} from '../utils/filters'
export default {
  name: 'orderDetails',
  data() {
    return {
      code: null,
      qrcode: false,
      obj: {},
      currentCare: {
        name: ''
      },
      newClick: true,
      show: false,
      showCares: false,
      reason: '',
      showrenew: false,
      renewMessage: '',
      showDay: false,
      showFinishOrder: false,
      orderText: '是否取消订单',
      daySlots: [
        {
          flex: 1,
          values: dayArr,
          className: 'slot1',
          textAlign: 'center'
        }],
      day: '',
      cares: [],
      defaultInfo: true,
      detailInfo: false
    }
  },
  computed:{
    ...mapGetters({
      orderDetail:'orderDetail'
    })
  },
  methods: {
     ...mapActions({
      getOrderDetail: 'getOrderDetail',
      changeTabSelected: 'changeTabSelected',
      getCurrentCare: 'getCurrentCare',
      changeCareFn: 'changeCare',
      addAppointment: 'addAppointment',
      addMyAppointment: 'addMyAppointment',
      updateOrder: 'updateOrder',
      getAssessments: 'getAssessments',
      addYSAppoinment: 'addYSAppoinment'
    }),
    handleClick: function() {
      this.showFinishOrder = true;
      if(this.orderDetail.navtitle == '提前结单') {
        this.orderText = '是否提前结单';
      } else if(this.orderDetail.navtitle == '取消结单'){
        this.orderText = '是否取消结单';
      }
    },
    handleCareClick: function(code, id, type) {
      let t = 2;
      if(type != 2) {
        t = 1;
      }
      this.$router.push({path: '/evaluate',query: {code: code, careId: id, type: t}})
    },
    handleshowFinishOrderRight: function() {
      let data = {};
      data.code = this.orderDetail.code;
      
      if(this.orderDetail.navtitle == '提前结单') {
        data.action = 'complete';
        this.updateOrder(data).then(response => {
          if(response.ok) {
            Toast({
                message: '结单成功',
                position: 'bottom',
                duration: 1000
              });
            this.showFinishOrder = false;
          }
        }, error => {
          Toast({
            message: '结单失败',
            position: 'bottom',
            duration: 1000
          });
          this.showFinishOrder = false;
        })
      } else if(this.orderDetail.navtitle == '取消订单'){
        data.action = 'cancel';
        this.updateOrder(data).then(response => {
          if(response.ok) {
            Toast({
                message: '取消成功',
                position: 'bottom',
                duration: 1000
              });
            this.showFinishOrder = false;
            this.$router.push('/index/order');
          }
        }, error => {
          Toast({
            message: '取消失败',
            position: 'bottom',
            duration: 1000
          });
          this.showFinishOrder = false;
        })
      }
    },
    showDayHandle: function() {
      if(this.orderDetail.serviceType == 2) {
        let obj = this.orderDetail;
        obj.fromOrder = this.orderDetail.id;
        obj.diseaseCare = {};
        obj.diseaseCare.serviceId = this.orderDetail.detail.id;
        obj.diseaseCare.selfCareAbility = this.orderDetail.detail.selfCareAbility;
        obj.diseaseCare.illnessLevel = this.orderDetail.detail.illnessLevel;
        this.obj = obj;
      } else if(this.orderDetail.serviceType == 3 || this.orderDetail.serviceType == 4) {
        let obj = this.orderDetail;
        obj.fromOrder = this.orderDetail.id;
        obj.infantMom = {};
        obj.infantMom.serviceId = this.orderDetail.detail.id;
        obj.infantMom.understanding = this.orderDetail.detail.understanding;
        obj.infantMom.maternalAge = this.orderDetail.detail.maternalAge;
        obj.infantMom.parturition = this.orderDetail.detail.parturition;
        obj.infantMom.infantCount = this.orderDetail.detail.infantCount;
        obj.infantMom.infantWeight = this.orderDetail.detail.infantWeight;
        obj.infantMom.infantHeight = this.orderDetail.detail.infantHeight;
        obj.infantMom.maternalCases = this.orderDetail.detail.maternalCases;
        obj.infantMom.babyCases = this.orderDetail.detail.babyCases;
        this.obj = obj;
      }
      // 半日服务  月嫂有没有半日服务
      if(this.orderDetail.serviceDuration < 1) {
        let sercviceDate = new Date(this.orderDetail.serviceDate).getTime() + 43200000;
        sercviceDate = timeFilter(sercviceDate);
        this.obj.serviceDate =sercviceDate;
        // 病患陪护
        if(this.orderDetail.serviceType == 2) {
          this.addAppointment(this.obj).then(res => {
            if(res.ok) {
              const renewKey = this.code + 'renew';
              localStorage.setItem(renewKey,true)
              Toast({
                message: '续约成功',
                position: 'bottom',
                duration: 1000
              });
            }
          },error => {
            Toast({
                message: '续约失败',
                position: 'bottom',
                duration: 1000
              });
          });
        } else if(this.orderDetail.serviceType == 3) {
          // 母婴陪护服务
          this.addMyAppointment(this.obj).then(res => {
            if(res.ok) {
              const renewKey = this.code + 'renew';
              localStorage.setItem(renewKey,true)
              Toast({
                message: '续约成功',
                position: 'bottom',
                duration: 1000
              });
            }
          }, error => {
            Toast({
                message: '续约失败',
                position: 'bottom',
                duration: 1000
              });
          })
        }
      } else if(this.orderDetail.serviceType == 4){
        // 月嫂服务
        this.obj.serviceDuration = this.orderDetail.serviceDuration * 2;
        this.addYSAppoinment(this.obj).then(res => {
          if(res.ok) {
              const renewKey = this.code + 'renew';
              localStorage.setItem(renewKey,true)
              Toast({
                message: '续约成功',
                position: 'bottom',
                duration: 1000
              });
          }
        }, error => {
          Toast({
                message: '续约失败',
                position: 'bottom',
                duration: 1000
              });
        });
      } else {
        this.showrenew = false;
        this.showDay = true;
      }
    },
    handleInnerSecondChange: function() {
      this.obj.serviceDuration = Number(this.orderDetail.serviceDuration) + Number(this.day);
      if(this.orderDetail.serviceType == 2) {
        this.addAppointment(this.obj).then(res => {
          if(res.ok) {
            const renewKey = this.code + 'renew';
            localStorage.setItem(renewKey,true)
            Toast({
              message: '续约成功',
              position: 'bottom',
              duration: 1000
            });
          }
        },error => {
          Toast({
              message: '续约失败',
              position: 'bottom',
              duration: 1000
            });
        });
      } else if(this.orderDetail.serviceType == 3) {
        this.addMyAppointment(this.obj).then(res => {
          if(res.ok) {
            const renewKey = this.code + 'renew';
            localStorage.setItem(renewKey,true)
            Toast({
              message: '续约成功',
              position: 'bottom',
              duration: 1000
            });
          }
        }, error => {
          Toast({
              message: '续约失败',
              position: 'bottom',
              duration: 1000
            });
        })
      }
      this.showDay = false;
    },
    onDayChange: function(picker, values) {
      this.day = values[0];
      
    },
    handleCancle: function() {
      this.show = false;
    },
    handleRight: function() {
      this.changeCareFn({code: this.code, reason: this.reason}).then(response => {
        this.newClick = false;
        Toast({
          message: '申请提交成功',
          position: 'bottom',
          duration: 1000
        });
        this.show = false;
      })
    },
    careInfo: function (code) {
      this.$router.push({path: '/extraOrderDetail', query: {code: code}});
    },
    changeCare: function () {
      if(this.newClick) {
        sessionStorage.setItem('currentCareId', this.currentCare.id);
        
        this.show = true;
      } else {
        Toast({
          message: '该订单已申请换工',
          position: 'bottom',
          duration: 1000
        });
      }
    },
    renew: function () {
      
      const renewKey = this.code + 'renew';
      if(localStorage.getItem(renewKey)) {
        Toast({
          message: '该订单已续约',
          position: 'bottom',
          duration: 1000
        });
      } else {
        //serviceDuration 时长
        const year = this.orderDetail.serviceDate.slice(0, 4);
        const month = this.orderDetail.serviceDate.slice(5, 7);
        const day = this.orderDetail.serviceDate.slice(8, 10);
        const hour = this.orderDetail.serviceDate.slice(11, 13);
        const minute = this.orderDetail.serviceDate.slice(14, 16);
        const dateStr = `${year}/${month}/${day} ${hour}:${minute}`;
        console.log(new Date(dateStr).getTime()+ 86400000 * this.orderDetail.serviceDuration);
        const date = new Date(dateStr).getTime() + 86400000 * this.orderDetail.serviceDuration;
        let str = timeFilter(date);
        str = `订单${str}到期，是否预约`;
        this.renewMessage = str;
        this.showrenew = true;
      }
    },
    evaluate: function () {
      // 
      // 1 表示为月嫂 
      // 2 表示为护工
      // 判断是否有多个评价 此处为api
      // 只有一个护工
      let arr = [];
      let length = 0;
      let type = 2;
      if(this.orderDetail.serviceType != 2) {
        type = 1;
      }
      this.getAssessments(this.code).then(response => {
        if(response.ok) {
          response.data.forEach(i => {
            if(i.assessments.length == 0) {
              arr.push(i.care);
            }
          })
          if(arr.length === 0) {
            Toast({
              message: '所有护工都已评价',
              position: 'bottom',
              duration: 1000
            });
          } else if(arr.length === 1) {
            this.$router.push(`/evaluate?code=${this.code}&careId=${arr[0].id}&type=${type}`);
          } else if(arr.length > 1) {
            this.cares = arr;
            this.showCares = true;
          }
        }
      })
    },
    back: function() {
      history.go(-1);
      this.changeTabSelected('订单');
    },
    showqrImg: function(img) {
      
      
      if(img) {
        this.qrcode = true;
      }
    },
    currentCareDetail: function(code) {
      this.$router.push({path: '/currentCareDetail', query: {code: code}});
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      
      vm.code = to.query.code;
      vm.getOrderDetail(to.query.code);
      vm.getCurrentCare(to.query.code).then(response => {
        vm.currentCare = response.data;
        if(Object.keys(vm.currentCare).length > 0) {
          vm.detailInfo = true;
          vm.defaultInfo = false;
          vm.currentCare = response.data;
          
          vm.currentCare.age = new Date().getFullYear() - response.data.birthday.slice(0,4);
        } else {
          vm.detailInfo = false;
          vm.defaultInfo = true;
        }
        const id = sessionStorage.getItem('currentCareId');
        if(id && id != response.data.id) {
          vm.newClick = true;
        }
      })
    })
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .orderDetail{
    font-size: 0.4rem;
  }
  .orderDetail .orderDetailBody {
    padding: 0.3rem;
  }
  .orderDetail .orderDetailBodyHeader {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5rem;
  }
  .orderDetail .orderDetailBodyHeader div {
    height: 0.8rem;
  }
  .orderDetail .orderDetailBodyHeader div img {
    height: 100%;
  }
  .orderDetailBodyMainHeader {
    display: flex;
    justify-content: space-between;
  }
  .orderDetailBodyMainHeader div{
    height: 1.2rem;
    line-height: 1.2rem;
  }
  .hgInfo img {
    width: 1rem;
    height: 1rem;
    vertical-align: middle;
    border-radius: 0.5rem;
  }
  .orderDetailBodyMainBody {
    margin-top: 0.2rem;
  }
  .orderDetailBodyMainBody div {
    display: flex;
    min-height: 0.8rem;
  }
  .orderDetailBodyMainBody div ul li{
    list-style: none;
  }
  .orderDetailBodyMainBody .t{
    min-width: 2rem;
  }
  .hgStatus {
    color: #565256;
  }
  .orderDetail .orderDetailBody .mint-popup {
    height: 7rem;
    width: 7rem;
  }
  .orderDetail .mint-popup {
     width: 65%;
  }
  .orderDetailbtns{
    padding: 0.3rem;
    display: flex;
    flex-wrap: wrap;
  }
  .orderDetailbtns .mint-button {
    width: 45%;
    margin-right: 0.3rem;
    margin-bottom: 0.3rem;
    box-shadow: none;
    background: #fff;
    border-radius: 10px; 
    border: 2px solid #d06091;
    color: #d06091;
  }
  .orderDetail .msgbox {
  }
  .orderDetail .w_field_core {
    margin-bottom: 0;
    width: 100%;
  }
  .orderDetail .m_btns .btn.btn_right {
    color: #d06091;
    width: 50%;
}
.cares .m_content {
  max-height: 6rem;
  padding: 1rem;
  overflow: scroll;
}
.cares .m_content img{
  width: 2rem;
  border-radius: 1rem;
  height: 2rem;
}
.cares .m_content .m_message {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.cares .m_content .m_message .info {
  width: 3rem;
  font-size: 0.4rem;
}
</style>



